<template>
  <div class="dialog-root" v-if="show" @click.self="show = false">
    <div class="dialog-view">
      <img
        class="dialog-close"
        @click="close"
        src="@/assets/icon-a/close-black.png"
        alt="close"
      />
      <div class="dialog-title">
        <span class="border"></span>
        <span>限量号设置</span>
      </div>
      <div class="dialog-content">
        <div class="line1">
          <div class="label">订单号</div>
          <div class="value">{{formData.orderNo}}</div>
        </div>
        <div class="line2">
          <div class="label">限量号设置</div>
          <input class="value" v-model="formData.limitedNo" placeholder="请输入内容" />
        </div>
      </div>
      <div class="dialog-foot">
        <div class="dialog-confirm" @click="confirm">保存</div>
        <div class="dialog-cancel" @click="close">取消</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    dialogShow: {
      value: Boolean,
      default: () => false,
    },
    options: {
      value: Object,
      default: () => {},
    },
  },
  data() {
    return {
      show: false,
      formData: null,
    };
  },
  watch: {
    dialogShow: {
      handler(newVal) {
        this.show = newVal;
      },
      immediate: true,
    },
  },
  created() {
    this.formData = this.options.contentData;
  },
  methods: {
    close() {
      this.$emit("close");
    },
    confirm() {
      this.$emit("confirm", this.formData);
    },
  },
};
</script>
<style scoped lang="scss">
.dialog-root {
  z-index: 99;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(#202020, 0.8);

  .dialog-view {
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    left: 50%;
    width: 518px;
    min-height: 100px;
    background: #ffffff;
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 18px;

    .dialog-close {
      cursor: pointer;
      width: 18px;
      height: 18px;
      position: absolute;
      right: 19px;
      top: 19px;
    }

    .dialog-title {
      width: 100%;
      font-weight: 500;
      font-size: 16px;
      color: #1a1a1a;
      display: flex;
      align-items: center;

      .border {
        width: 4px;
        height: 18px;
        margin-right: 8px;
        background-color: #f50537;
        border-radius: 40px;
      }
    }

    .dialog-content {
      width: 100%;
      padding: 45px 6px 34px;
      text-align: left;

      .line1 {
        .label {
          font-weight: 400;
          font-size: 14px;
          line-height: 22px;
          color: #999999;
        }

        .value {
          margin-top: 8px;
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          color: #666666;
        }
      }

      .line2 {
        margin-top: 32px;
        .label {
          font-weight: 400;
          font-size: 14px;
          line-height: 22px;
          color: #1a1a1a;
        }
        .value {
          box-sizing: border-box;
          width: 471px;
          height: 37px;
          margin-top: 8px;
          padding: 0 10px;
          background-color: #ffffff;
          color: #999999;
          border: none;
          border: 1px solid #d9d9d9;
          border-radius: 2px;
        }

        input:focus {
            border-color: #f50537!important;
        }
      }
    }

    .dialog-foot {
      width: 100%;
      padding: 0 10px;
      display: flex;
      justify-content: flex-end;
      align-items: center;

      .dialog-confirm {
        cursor: pointer;
        margin-right: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 112px;
        height: 37px;
        background-color: #f50537;
        border-radius: 2px;
        color: #ffffff;
      }
      .dialog-confirm:active {
        background-color: #fa335c;
        opacity: 0.95;
      }

      .dialog-cancel {
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 112px;
        height: 37px;
        color: #000000;
        border-radius: 2px;
        border: 1px solid #dddddd;
      }

      .dialog-cancel:active {
        color: #4a4a4a;
        opacity: 0.95;
      }
    }
  }
}
</style>
